* {
  box-sizing: border-box;
}

body {
  background-color: beige;
  margin: 0;
}

input img {
  max-width: 100%;
  height: auto;
  display: block;
}

.form-wrapper {
  width: 100%;
  height: 65px;
  background-color: rgba(8, 70, 218, .6);
  justify-content: center;
  margin-bottom: 20px;
  padding: 10px 0;
  display: flex;
  position: relative;
}

.search-input {
  width: 300px;
  opacity: .4;
  z-index: 1;
  background-color: beige;
  border-radius: 10px;
  flex-wrap: nowrap;
  justify-content: center;
  display: flex;
  position: fixed;
  top: 5px;
  overflow: hidden;
}

.search-input:hover {
  opacity: 1;
}

.input-place {
  text-align: center;
  background-color: beige;
  border: none;
  font-size: 20px;
}

.input-place:focus {
  border: none;
  outline: rgba(0, 0, 0, 0);
}

.search-btn {
  cursor: pointer;
  background-color: beige;
  border: none;
  padding: 15px;
}

.search-btn:focus {
  outline: rgba(0, 0, 0, 0);
}

.svg-current {
  fill: currentColor;
}

@media (max-width: 767px) {
  .input-place {
    width: 250px;
  }
}

.search-gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.info {
  justify-content: space-around;
  padding: 10px;
  font-size: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

@media (max-width: 999px) {
  .wrapper {
    padding: 0 8px;
  }

  .container {
    width: 250px;
    height: 250px;
  }

  .photo-card {
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 1px 6px 8px 2px rgba(173, 173, 173, .8);
  }

  .container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .info {
    flex-wrap: wrap;
    gap: 5px;
    padding: 5px;
    font-size: 7px;
    display: flex;
  }
}

@media (min-width: 1000px) {
  .wrapper {
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
  }

  .photo-card {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    transition: transform .3s cubic-bezier(.25, .46, .45, .94);
    box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
  }

  .container {
    width: 100%;
    height: 250px;
  }

  .container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .container img:hover {
    cursor: zoom-in;
    transform: scale(1.03);
    box-shadow: 1px 6px 8px 2px rgba(173, 173, 173, .8);
  }
}



